import React from 'react';
import { MDBNavbar, MDBNavbarBrand, MDBNavbarNav, MDBNavbarToggler, MDBCollapse, MDBNavItem, MDBNavLink, MDBContainer, MDBMask, MDBView } from 'mdbreact';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Login from "./Login";

class Header extends React.Component {
    constructor(props) {
        super(props);
        let path = window.location.pathname;
        path = path.replace(/\/belot/g, "");
        this.state = {
            collapse: false,
            isWideEnough: false,
            activeItemPills: path
        };
        this.onClick = this.onClick.bind(this);
    }

    onClick() {
        this.setState({
            collapse: !this.state.collapse
        });
    }

    togglePills = tab => () => {
        const { activePills } = this.state;
        if (activePills !== tab) {
            this.setState({
                activeItemPills: tab
            });
        }
    };

    render() {
        const { activeItemPills } = this.state;
        return (
            <div>
                <header>
                    <Router basename={process.env.PUBLIC_URL}>
                        <MDBNavbar color="unique-color" dark expand="md" fixed="top">
                            <MDBNavbarBrand href="/" onClick={this.togglePills('/')}>
                                <strong>Belot</strong>
                            </MDBNavbarBrand>
                            {!this.state.isWideEnough && <MDBNavbarToggler onClick={this.onClick} />}
                            <MDBCollapse isOpen={this.state.collapse} navbar>
                                <MDBNavbarNav left>
                                    <MDBNavItem active={activeItemPills === '/'} onClick={this.togglePills('/')}>
                                        <MDBNavLink to="/">Home</MDBNavLink>
                                    </MDBNavItem>
                                    <MDBNavItem active={activeItemPills === '/user'} onClick={this.togglePills('/user')}>
                                        <MDBNavLink to="/user">Profile</MDBNavLink>
                                    </MDBNavItem>
                                </MDBNavbarNav>
                            </MDBCollapse>
                        </MDBNavbar>

                        <Switch>
                            <Route exact path="/user">
                                <Login />
                            </Route>
                        </Switch>
                    </Router>
                </header>
            </div>
        );
    }
}

export default Header;